#registration {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: row;
}

#registrationContainer {
	width: 50%;
	height: 100%;
	margin: 1rem;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

#registrationStyleDiv {
	width: 50%;
	height: 100%;
	background-color: rgb(101, 176, 238);
	border: 3px black solid;
	margin: 1rem;
}

.registerH1 {
	font-size: 3rem;
	margin-bottom: 4rem;
	margin-left: 5rem;
}

.registerH2 {
	margin-bottom: 3rem;
	margin-left: 5rem;
	font-size: 2rem;
}
*/ form {
	display: flex;
	flex-direction: column;
	margin-left: 5rem;
	/* font-size: 2rem; */
	position: relative;
}

.registrationSpan {
	margin-bottom: 1.5rem;
}

label {
	display: inline-block;
	width: 10rem;
	text-align: start;
	margin-right: 2rem;
}

input {
	width: 15rem;
	height: 1.5rem;
	vertical-align: middle;
}

.registerSubmit {
	border: 5px gray outset;
	background-color: transparent;
	height: 3rem;
	width: 12rem;
	font-size: 1.5rem;
	position: absolute;
	bottom: -5rem;
	right: -2rem;
}
