.body {
	font-family: "Clear Sans", sans-serif;
	margin: 0;
	padding: 0;
}
#talent {
	width: 90%;
}
.talentCard {
	flex-direction: row;
}
.container {
	width: 90%;
	margin-bottom: 3rem;
}
.spotlightInfo {
	height: 4rem;
	padding-left: 0;
	margin-left: 5%;
	margin-bottom: 3rem;
	margin-top: 2rem;
}

/* .title {
    marginBottom: 10px;
    fontWeight: bold;
    fontStyle: italic;
    
} */

.title > h2 {
	position: absolute;
	left: 4.7rem;
}

.flex-container {
	padding-top: 100px;
	padding-left: 30px;
	padding-bottom: 25px;
	display: flex;
	flex-direction: row;
	grid-gap: 90px;
	flex-wrap: wrap;
	box-shadow: rgb(0 0 0 / 10%) 0px 10px 15px -3px, rgb(0 0 0 / 5%) 0px 4px 6px -2px;
	max-width: 100%;
	height: 25rem;
	border-style: solid;
	border-radius: 0.625rem;
	border: 0px #d4d4d4 solid;
	background-color: #fff;

	flex-shrink: 2;

	justify-content: flex-start;
	align-items: center;
	position: relative;
	margin-bottom: 3rem;
}

.flex2 {
	display: flex;
	padding: 30px;
	flex-direction: row;
	justify-content: center;
	grid-gap: 10px;
	flex-wrap: wrap;
	box-shadow: 0px 0px 5px 0px black;
	width: 90%;
	height: auto;
	/* margin-left: auto;
    margin-right: auto; */
	background-color: #ededed;
	display: flex;
	justify-content: space-between;
	align-items: center;

	/* display: flex;
    height: auto;
    /* padding: 30px; */
	flex-direction: row;
	justify-content: center;
	/* grid-gap: 10px; */
	flex-wrap: nowrap;
	box-shadow: 0px 1px 5px 0px black;
	width: 90%;
	/* margin-left: auto;
    margin-right: auto; */
	background-color: #ededed;
	margin-bottom: 3rem;
}

.topCard {
	width: 250px;
	height: 400px;
	background: white;
	border-radius: 10px;
	box-shadow: 0px 1px 10px black;
}

.topCard p {
	text-align: center;
	font-size: 12px;
	padding: 15px 10px 10px 20px;
	text-align: center;
}
.topCard button {
	justify-content: center;
	padding: 10px;
	font-size: 12px;
	align-items: center;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.topCard image {
	justify-content: center;
}
.topCard h3 {
	font-weight: bold;
	text-align: center;
	font-size: 20px;
	padding: 10px;
}

.topCard h4 {
	text-align: center;

	color: #2465d4;
	font-style: italic;
}

.image {
	border-radius: 50%;
	align-items: center;
	display: block;
	margin-left: auto;
	margin-right: auto;
	padding: 10px;
}

.flexContainerh1 {
	position: absolute;
	top: 2rem;
}
